import React from 'react';
import {Card, Elevation, Text, Intent, Tag, Colors} from '@blueprintjs/core';
import {ResponsiveContainer, ComposedChart, Line, Pie, PieChart, Bar, XAxis, YAxis, CartesianGrid, Tooltip, Legend, Sector, Cell} from 'recharts';
import "./index.css";
import {onMachinesUpdated, initListeners} from "../../actions/socket/MachinesActions";
import PropTypes from "prop-types";
import {bindActionCreators} from "redux";
import {loadMachines} from "../../actions/MachinesActions";
import {loadCenters} from "../../actions/CentersActions";
import {loadCards} from "../../actions/Cards";
import {loadRibbons} from "../../actions/Ribbons";
import {loadLaminates} from "../../actions/Laminates";
import {loadReceiptPaperRolls} from "../../actions/ReceiptPaperRolls";
import {connect} from "react-redux";
import _first from 'lodash/first';
import _last from 'lodash/last';
import _isEmpty from 'lodash/isEmpty';

const data = [{day: 'Monday', seconds: 590},
              {day: 'Tuesday', seconds: 868},
              {day: 'Wednesday', seconds: 1397},
              {day: 'Thursday', seconds: 1480},
              {day: 'Friday', seconds: 0}];

const COLORS = ['#0088FE', "#9BBF30", '#0F9960', '#DB3737', '#7157D9'];

const renderActiveShape = (props) => {
    const RADIAN = Math.PI / 180;
    const { cx, cy, midAngle, innerRadius, outerRadius, startAngle, endAngle,
      fill, payload, percent, value } = props;
    const sin = Math.sin(-RADIAN * midAngle);
    const cos = Math.cos(-RADIAN * midAngle);
    const sx = cx + (outerRadius + 10) * cos;
    const sy = cy + (outerRadius + 10) * sin;
    const mx = cx + (outerRadius + 30) * cos;
    const my = cy + (outerRadius + 30) * sin;
    const ex = mx + (cos >= 0 ? 1 : -1) * 22;
    const ey = my;
    const textAnchor = cos >= 0 ? 'start' : 'end';
  
    return (
      <g>
        <text x={cx} y={cy} dy={8} textAnchor="middle" fill={fill}>{payload.name}</text>
        <Sector
          cx={cx}
          cy={cy}
          innerRadius={innerRadius}
          outerRadius={outerRadius}
          startAngle={startAngle}
          endAngle={endAngle}
          fill={fill}
        />
        <Sector
          cx={cx}
          cy={cy}
          startAngle={startAngle}
          endAngle={endAngle}
          innerRadius={outerRadius + 6}
          outerRadius={outerRadius + 10}
          fill={fill}
        />
        <path d={`M${sx},${sy}L${mx},${my}L${ex},${ey}`} stroke={fill} fill="none"/>
        <circle cx={ex} cy={ey} r={2} fill={fill} stroke="none"/>
        <text x={ex + (cos >= 0 ? 1 : -1) * 12} y={ey} textAnchor={textAnchor} fill="#333">{`Center(s) ${value}`}</text>
        <text x={ex + (cos >= 0 ? 1 : -1) * 12} y={ey} dy={18} textAnchor={textAnchor} fill="#999">
          {`(${(percent * 100).toFixed(2)}%)`}
        </text>
      </g>
    );
  };


class TwoLevelPieChart extends React.Component{
    
    state = {
        activeIndex: 0,
    };

  onPieEnter = (data, index) => {
      this.setState({activeIndex: index})
  };

  render(){

    return (
        <ResponsiveContainer>
            <PieChart width={800} height={400}>
                <Pie 
                activeIndex={this.state.activeIndex}
                activeShape={renderActiveShape} 
                data={this.props.data} 
                innerRadius={60}
                outerRadius={80} 
                fill="#8884d8"
                onMouseEnter={this.onPieEnter}
                >
                    {
                        this.props.data.map((entry, index) => <Cell fill={COLORS[index % COLORS.length]}/>)
                    }
                </Pie>
            </PieChart>
        </ResponsiveContainer>
    );
  }
}

class Dashboard extends React.Component{

    componentDidMount(){
        const {userChannel, onMachinesUpdated, loadMachines, authUser: {token}, 
            loadCards, loadCenters, loadLaminates, loadReceiptPaperRolls, loadRibbons} = this.props;
        initListeners({channel: userChannel, onMachinesUpdated});

        loadMachines(token, {});
        loadCards(token, {});
        loadCenters(token, {});
        loadLaminates(token, {});
        loadReceiptPaperRolls(token, {});
        loadRibbons(token, {});
    }

    _extractCentersOverview = () => {
        const {centers} = this.props;

    }
    _extractCardsOverview = () => {
        const {cardTransactions} = this.props;
        return _isEmpty(cardTransactions) ? 0 : _last(cardTransactions).stock_after_transaction;
    }
    _extractRibbonsOverview = () => {
        const {ribbonTransactions} = this.props;
        return _isEmpty(ribbonTransactions) ? 0 : _last(ribbonTransactions).stock_after_transaction;
    }
    _extractLaminatesOverview = () => {
        const {laminateTransactions} = this.props;
        return _isEmpty(laminateTransactions) ? 0 : _last(laminateTransactions).stock_after_transaction;
    }
    _extractRollsOverview = () => {
        const {receiptPaperRollTransactions} = this.props;
        return _isEmpty(receiptPaperRollTransactions) ? 0 : _last(receiptPaperRollTransactions).stock_after_transaction;
    }

    render(){

        const {machines, centers} = this.props;
        const actives = machines.filter(({power}) => power === "ON").length;
        const inactives = machines.filter(({power}) => power === "OFF").length;

        const UptimeChart = () => {
            return (
                <ResponsiveContainer>
                    <ComposedChart width={600} height={400} data={data}
                            margin={{top: 20, right: 20, bottom: 20, left: 20}}>
                        <CartesianGrid stroke='#f5f5f5'/>
                        <XAxis dataKey="day"/>
                        <YAxis />
                        <Tooltip/>
                        <Legend/>
                        <Bar dataKey='seconds' barSize={20} fill='#413ea0'/>
                        <Line type='monotone' dataKey='seconds' stroke='#ff7300'/>
                    </ComposedChart>
               </ResponsiveContainer>
            );
        };

        const DowntimeChart = () => {
            return (
                <ResponsiveContainer>
                    <ComposedChart width={600} height={400} data={data}
                            margin={{top: 20, right: 20, bottom: 20, left: 20}}>
                        <CartesianGrid stroke='#f5f5f5'/>
                        <XAxis dataKey="day"/>
                        <YAxis />
                        <Tooltip/>
                        <Legend/>
                        <Bar dataKey='seconds' barSize={20} fill='#413ea0'/>
                        <Line type='monotone' dataKey='seconds' stroke='#ff7300'/>
                    </ComposedChart>
               </ResponsiveContainer>
            );
        };

        // const {} = this._extractCentersOverview();

        const activeCenters = centers.filter(({status}) => status === 2)
        const inactiveCenters = centers.filter(({status}) => status === 3)
        const newCenters = centers.filter(({status}) => status === 0)
        const closedCenters = centers.filter(({status}) => status === 4)
        const deployedCenters = centers.filter(({status}) => status === 1)

        const centersOverview = [
            {name: 'New', value: newCenters.length}, 
            {name: 'Deployed', value: deployedCenters.length}, 
            {name: 'Active', value: activeCenters.length}, 
            {name: 'Inactive', value: inactiveCenters.length},
            {name: 'Closed', value: closedCenters.length}
        ];

        return (
            <div className="dashboard-page">

                <Card className="box">
                    
                    <div className="row active">

                        <Card interactive elevation={Elevation.ONE} className="item">
                            <h3 className="item-header"><Text ellipsize={true}>Machines</Text></h3>
                            <div className="item-content">
                                <div className="status-off">
                                    <h4 className="header"><Text ellipsize={true}>Active</Text></h4>
                                    <p className="value" style={{color: Colors.GREEN5}}>
                                        {actives}
                                    </p>
                                </div>
                                <div className="status-on">
                                    <h4 className="header"><Text ellipsize={true}>Inactive</Text></h4>
                                    <p className="value" style={{color: Colors.RED5}}>
                                        {inactives}
                                    </p>
                                </div>
                            </div>
                        </Card>

                        <Card interactive elevation={Elevation.ONE} className="item">
                            <h3 className="item-header"><Text ellipsize={true}>Processes</Text></h3>
                            <div className="item-content">
                                <div className="services">
                                    <h4 className="header"><Text ellipsize={true}>Services</Text></h4>
                                    <p className="value">
                                        0
                                    </p>
                                </div>
                                <div className="applications">
                                    <h4 className="header"><Text ellipsize={true}>Applications</Text></h4>
                                    <p className="value">
                                        0
                                    </p>
                                </div>
                            </div>
                        </Card>

                    </div>

                    <Card interactive elevation={Elevation.ONE} className="row certificates">
                        <h3 className="row-header"><Text ellipsize={true}>Certificates</Text></h3>
                        <div className="row-content">
                                <div className="valid">
                                    <h4 className="header"><Text ellipsize={true}>Valid</Text></h4>
                                    <p className="value">
                                        0
                                    </p>
                                </div>
                                <div className="due">
                                    <h4 className="header"><Text ellipsize={true}>Due</Text></h4>
                                    <p className="value" style={{color: Colors.ORANGE5}}>
                                        0
                                    </p>
                                </div>
                            </div>
                    </Card>

                    <div className="row consumables">
                        <Card interactive elevation={Elevation.ONE} className="item">
                            <h3 className="item-header"><Text ellipsize={true}>Blank Cards</Text></h3>
                            <div className="item-content">
                                <div className="left">
                                    <h4 className="header"><Text ellipsize={true}>In Stock</Text></h4>
                                    <p className="value">
                                        {this._extractCardsOverview()}
                                    </p>
                                </div>
                            </div>
                        </Card>
                        <Card interactive elevation={Elevation.ONE} className="item">
                            <h3 className="item-header"><Text ellipsize={true}>Ribbons</Text></h3>
                            <div className="item-content">
                                <div className="left">
                                    <h4 className="header"><Text ellipsize={true}>In Stock</Text></h4>
                                    <p className="value">
                                        {this._extractRibbonsOverview()}
                                    </p>
                                </div>
                            </div>
                        </Card>
                    </div>

                    <div className="row consumables">
                        <Card interactive elevation={Elevation.ONE} className="item">
                            <h3 className="item-header"><Text ellipsize={true}>Laminates</Text></h3>
                            <div className="item-content">
                                <div className="left">
                                    <h4 className="header"><Text ellipsize={true}>In Stock</Text></h4>
                                    <p className="value">
                                        {this._extractLaminatesOverview()}
                                    </p>
                                </div>
                            </div>
                        </Card>
                        <Card interactive elevation={Elevation.ONE} className="item">
                            <h3 className="item-header"><Text ellipsize={true}>Receipt Paper Roll</Text></h3>
                            <div className="item-content">
                                <div className="left">
                                    <h4 className="header"><Text ellipsize={true}>In Stock</Text></h4>
                                    <p className="value">
                                        {this._extractRollsOverview()}
                                    </p>
                                </div>
                            </div>
                        </Card>
                    </div>

                </Card>

                <Card className="box centers-info">
                    <Card interactive elevation={Elevation.ONE} className="row centers">
                        <h3 className="row-header"><Text ellipsize={true}>Centers</Text></h3>
                        <div className="row-content">
                            <div className="total">
                                <h4 className="header"><Text ellipsize={true}>Total</Text></h4>
                                <p className="value">
                                    {centers.length}
                                </p>
                            </div>
                            <div className="running">
                                <h4 className="header"><Text ellipsize={true}>Active</Text></h4>
                                <p className="value" style={{color: Colors.GREEN5}}>
                                    {activeCenters.length}
                                </p>
                            </div>
                            <div className="closed">
                                <h4 className="header"><Text ellipsize={true}>Inactive</Text></h4>
                                <p className="value" style={{color: Colors.RED5}}>
                                    {inactiveCenters.length}
                                </p>
                            </div>
                        </div>
                    </Card>
                    <Card interactive elevation={Elevation.ONE} className="row centers-status-list">
                        <div className="row-header">
                            <div className="Name">Code</div>
                            <div className="Computers">Machines</div>
                            <div className="Status">Status</div>
                        </div>
                        <div className="row-content">
                            {
                                activeCenters.map(center => {
                                    return (
                                        <div className="center">
                                            <div className="Name">{center.code}</div>
                                            <div className="Computers">
                                                {
                                                    center.deployments[0] 
                                                        && `${center.deployments[0].machines.filter(({power}) => power !== "OFF").length} 
                                                        / ${center.deployments[0].machines.length}`
                                                }
                                            </div>
                                            <div className="Status">
                                                <Tag minimal intent={center.status === 1 ? Intent.SUCCESS : Intent.DANGER}>
                                                    {center.status === 1 ? "Running" : "Closed"}
                                                </Tag>
                                            </div>
                                        </div>
                                    );
                                })
                            }
                        </div>
                    </Card>
                </Card>

                <Card className="box graphs">
                    <Card interactive elevation={Elevation.ONE} className="row uptime">
                        <h3 className="row-header"><Text ellipsize={true}>Centers Uptime</Text></h3>
                        <div className="row-content">
                            <UptimeChart/>
                        </div>
                    </Card>

                    <Card interactive elevation={Elevation.ONE} className="row downtime">
                        <h3 className="row-header"><Text ellipsize={true}>Centers Downtime</Text></h3>
                        <div className="row-content">
                            <DowntimeChart/>
                        </div>
                    </Card>

                    <Card interactive elevation={Elevation.ONE} className="row centers-status">
                        <h3 className="row-header"><Text ellipsize={true}>Centers Stats</Text></h3>
                        <div className="row-content">
                            <TwoLevelPieChart data={centersOverview}/>
                        </div>
                    </Card>
                </Card>
            </div>
        );
    }

    static propTypes = {
        userChannel: PropTypes.object.isRequired,
        authUser: PropTypes.object.isRequired,
        onMachinesUpdated: PropTypes.func.isRequired,
        loadMachines: PropTypes.func.isRequired,

        loadCards: PropTypes.func.isRequired,
        loadCenters: PropTypes.func.isRequired,
        loadLaminates: PropTypes.func.isRequired,
        loadReceiptPaperRolls: PropTypes.func.isRequired,
        loadRibbons: PropTypes.func.isRequired,

        machines: PropTypes.array.isRequired,

        cardTransactions: PropTypes.array.isRequired,
        ribbonTransactions: PropTypes.array.isRequired,
        laminateTransactions: PropTypes.array.isRequired,
        centers: PropTypes.array.isRequired,
        receiptPaperRollTransactions: PropTypes.array.isRequired,
    }
}

const mapStateToProps = ({authUser, userChannel, machines, cardTransactions, ribbonTransactions, laminateTransactions, 
    centers, receiptPaperRollTransactions}) => ({
    authUser, userChannel, machines, cardTransactions, ribbonTransactions, laminateTransactions, centers, receiptPaperRollTransactions});
const mapDispatchToProps = dispatch => bindActionCreators({onMachinesUpdated, loadMachines, 
    loadCards, loadCenters, loadLaminates, loadReceiptPaperRolls, loadRibbons}, dispatch);

export default connect(mapStateToProps, mapDispatchToProps)(Dashboard);


